/** @format */

import React from "react";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <>
      <nav className="navbar navbar-fluid navbar-expand-lg navbar-light bg-info">
        <div className="container-fluid">
          <NavLink className="navbar-brand title" to="/">
            <h1 className="text-uppercase">
              <span className="text-warning ">R</span> Pharmacy
            </h1>
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav m-md-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink
                  className="nav-link active  me-5 p1 "
                  aria-current="page"
                  to="/"
                  activeClassName="highlight"
                  exact
                >
                  Home
                </NavLink>
              </li>

              <li className="nav-item dropdown">
                <NavLink
                  className="nav-link me-5 dropdown-toggle p1"
                  to="#"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  activeClassName="highlight"
                  exact
                >
                  Study Material
                </NavLink>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <NavLink className="dropdown-item" to="/">
                      Notes
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item " to="/">
                      Books
                    </NavLink>
                  </li>
                </ul>
              </li>
              <li className="nav-item">
                <NavLink
                  className="nav-link me-5 p1"
                  to="/about"
                  activeClassName="highlight"
                  exact
                >
                  About
                </NavLink>
              </li>
            </ul>
            <form class="d-flex">
              <input
                class="form-control me-2"
                type="search"
                placeholder=" Books"
                aria-label="Search"
              />
              <button class="btn btn-outline-success" type="submit">
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
