/** @format */

import React from "react";
import { NavLink } from "react-router-dom";

const Sem1 = () => {
  return (
    <>
      <div className="container my-5 sem ">
        <div className="contain">
          <h2 className="text-center">Sem 1</h2>
          <div className="row my-5">
            <div className="col-lg-6 col-md-6 col-12 col-xxl-6   text-center">
              <NavLink to="Hap" exact>
                <button
                  type="button "
                  className="semsub btn btn-outline-warning  "
                >
                  HAP 1
                </button>
              </NavLink>
            </div>
            <div className="col-lg-6 col-md-6 col-12 col-xxl-6 text-center ">
              <NavLink to="Pa" exact>
                <button
                  type="button "
                  className="semsub btn btn-outline-warning  "
                >
                  PA
                </button>
              </NavLink>
            </div>
          </div>
          <div className="row my-5 ">
            <div className="col-lg-6 col-md-6 col-12 col-xxl-6   text-center">
              <NavLink to="/Pharmaceutics" exact>
                <button
                  type="button "
                  className="semsub btn btn-outline-warning  "
                >
                  PHARMACEUTICS- I
                </button>
              </NavLink>
            </div>
            <div className="col-lg-6 col-md-6 col-12 col-xxl-6 text-center ">
              <NavLink to="/Pic" exact>
                <button
                  type="button "
                  className="semsub btn btn-outline-warning  "
                >
                  PIC
                </button>
              </NavLink>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sem1;
